    createDate5(){
      const form = new FormData();
      form.append("ob_nomi", this.formData5.ob_nomi);
      form.append("joyi", this.formData5.joyi);
      form.append("region_name", this.formData.region_name.nameuzlatn);
      form.append("district", this.formData.district.name);
      form.append("parent_code", this.formData.region_name.soatocode);
      form.append("code", this.formData.district.soatocode);
      form.append("maydon", this.formData5.maydon);
      form.append("hujjat", this.formData5.hujjat);
      form.append("mintaqa", this.formData5.mintaqa);
      form.append("ru_beril", this.formData5.ru_beril);
      form.append("ru_berilmay", this.formData5.ru_berilmay);
      form.append("desc", this.formData5.desc);
      form.append("latitude", this.formData5.latitude);
      form.append("longitude", this.formData5.longitude);
       this.$http.post('/cultural_heritage/', form,
          {
            headers: {
              Authorization: `Bearer ${localStorage.getItem('token')}`,
            },
          }
      )
          .then(()=>{
            this.$toast(`Malumot kiritildi`, {
                timeout: 2000,
                type:"success"
              });
          })

          .catch(error => {
            console.log(error.message)
             this.$toast(`${error.message} Malumotlar to'liq kiritilmagan`, {
                timeout: 2000,
                type:"error"
              });

          })

    },
    trySubmitFile1(e) {
      for (let i = 0; i < e.target.files.length; i++) {
        this.files_name1.push(e.target.files[i])
      }
      var files = Array.from(e.target.files);
      console.log(typeof (files))
      files.forEach((file) => {
        this.myImages1.push(file);
        var reader = new FileReader();
        var vm = this;
        reader.onload = function (e) {
          vm.imageUrl1.push(e.target.result);
        };
        reader.readAsDataURL(file);
      });
    },
    getDesc() {
      console.log(destricts)
      this.destricts=[]
      destricts.result.filter(tree => tree.oblastid === this.formData.region_name.id).forEach(el => {
        this.destricts.push({
          soatocode: el.soatocode,
          name: el.nameuzlatn
        })
      })
    }
  }
}
</script>